import React, { PureComponent } from 'react';
import * as actions from './actions/actionIndex';

export default class Registration extends React.PureComponent {
    handleSubmit(event) {
        event.preventDefault();
        const newPlayer = {
            facebookId: facebookId,
            screenName: $('#inputScreenName').val(),
        };
        actions.newPlayerRegistration(newPlayer);
    }

    render() {
        return (
            <div>
                <p>Please enter a screen name</p>
                <input id="inputScreenName" type="text" />
                <input id="submitScreenName" type="submit" value="submit" />
            </div>
        );
    }
}



